import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

export default function TDashboard(props) {
  const [loaded, setLoaded] = useState(false);
  useEffect(() => {
    if (!loaded) {
      refreshCourses();
      setLoaded(true);
    }
  });
  const navigate = useNavigate();
  const navigateToCourse = () => {
    navigate("/Course_classes");
  };
  const refreshCourses = () => {
    props.api
      .post("/refreshCourses", {
        id: localStorage.getItem("Tmail"),
        password: localStorage.getItem("Tpassword"),
      })
      .then((res) => {
        let corslst = res.data;
        corslst.reverse();
        setCoursesList(corslst);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  window.onload = () => {
    refreshCourses();
  };
  const [courseName, setCourseName] = useState("");
  const [uniqueId, setUniqueID] = useState("");
  const [myCoursesData, setMyCoursesData] = useState(0);
  const [addCourse, setAddCourse] = useState(0);
  const [status, setStatus] = useState("");
  const [coursesList, setCoursesList] = useState([]);
  return (
    <>
      <div className="container mt-3">
        <div className="mb-3">
          <div className="mb-3">
            <div className="sent-message">
              {localStorage.getItem("Tname")
                ? "Welcome, " + localStorage.getItem("Tname")
                : ""}
              <br />
              {localStorage.getItem("Tmail")
                ? "Email: " + localStorage.getItem("Tmail")
                : ""}
            </div>
          </div>
          <div className="mb-4">
            <div>
              <button onClick={refreshCourses} className="btn button2 mb-3">
                Refresh
              </button>
            </div>
            <button
              onClick={() => {
                myCoursesData === 0 ? setMyCoursesData(1) : setMyCoursesData(0);
              }}
              className="btn br-none w-100 border-0"
            >
              My Courses {myCoursesData === 0 ? "" : ""}
            </button>
            {myCoursesData === 0 ? (
              ""
            ) : (
              <div
                className={
                  "sent-message " + (coursesList.length > 0 ? "br-none" : "")
                }
              >
                {coursesList.length > 0 ? (
                  <table className="table text-white">
                    <thead>
                      <tr>
                        <th className="bg-dark" scope="col">
                          Unique ID
                        </th>
                        <th className="bg-dark" scope="col">
                          Course Name
                        </th>
                      </tr>
                    </thead>
                    {coursesList.map((data) => (
                      <tbody
                        style={{ cursor: "pointer" }}
                        onClick={() => {
                          localStorage.setItem("corskey", data[0]);
                          localStorage.setItem("corsname", data[1]);
                          navigateToCourse();
                        }}
                        key={data[0]}
                      >
                        <tr>
                          <td>{data[0]}</td>
                          <td>{data[1]}</td>
                        </tr>
                      </tbody>
                    ))}
                  </table>
                ) : (
                  "Create a course to get started."
                )}
              </div>
            )}
          </div>
          <div>
            <button
              onClick={() => {
                addCourse === 0 ? setAddCourse(1) : setAddCourse(0);
              }}
              className="btn w-100 border-0"
            >
              Create Course {addCourse === 0 ? "" : ""}
            </button>
            {addCourse === 0 ? (
              ""
            ) : (
              <>
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    props.api
                      .post("/createCourse", {
                        courseName: courseName,
                        id: uniqueId,
                        email: localStorage.getItem("Tmail"),
                        password: localStorage.getItem("Tpassword"),
                      })
                      .then((res) => {
                        setStatus(res.data);
                      })
                      .catch((e) => {
                        setStatus("Server did not respond.");
                      });
                  }}
                >
                  <div className="">
                    <input
                      required
                      type="text"
                      placeholder="Course Name"
                      className="rounded-top form-control border-secondary text-center bg-dark border-bottom-0"
                      onInput={(e) => setCourseName(e.target.value)}
                    />
                    <input
                      required
                      className="rounded-bottom form-control border-secondary text-center bg-dark"
                      type="text"
                      placeholder="Unique key"
                      onInput={(e) => setUniqueID(e.target.value)}
                    />
                  </div>
                  <button className="btn button2">Create Course</button>
                  <div className="sent-message">{status}</div>
                </form>
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
}
