import { useEffect } from "react";
import { useState } from "react";

export default function LoggedIn(props) {
  const [res, setRes] = useState("");

  const getlocation = () => {
    navigator.geolocation.getCurrentPosition(
      (position) => {
        props.api
          .post("/userLocation", {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
            Smail: localStorage.getItem("Smail"),
            Spassword: localStorage.getItem("Spassword"),
            scorsname: localStorage.getItem("scorsname"),
            scorskey: localStorage.getItem("scorskey"),
          })
          .then((res) => {
            setRes(res.data);
          })
          .catch((err) => {
            setRes("Server did not respond.");
          });
      },
      (error) => {
        setRes("Location not accessible");
      }
    );
  };
  const [l, setL] = useState(false);
  useEffect(() => {
    if (!l) {
      getlocation();
      setL(true);
    }
  });

  return (
    <>
      <div className="container mt-5">
        <div className="mb-2">
          Please stay on the page, so that your location can become accurate,
          otherwise your attendance will not be marked. <br />
          Your Email Address is:{" "}
          {localStorage.getItem("Smail")
            ? localStorage.getItem("Smail")
            : "please log in."}
        </div>
        <div>
          <button onClick={getlocation} className="btn button2 mb-3">
            Mark Attendance
          </button>
          <div className="sent-message">{res}</div>
        </div>
      </div>
    </>
  );
}
