import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Collapse from "react-bootstrap/Collapse";

export default function TClasses(props) {
  const currdate = () => {
    const separator = "/";
    let newDate = new Date();
    let date = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();
    return `${date}${separator}${
      month < 10 ? `0${month}` : `${month}`
    }${separator}${year}`;
  };
  const navigate = useNavigate();
  const navigateToCourse = () => {
    navigate("/Course_Dashboard");
  };
  const [newClassName, setNewClassName] = useState(currdate());
  const [coursesList, setCoursesList] = useState("");
  const [myCoursesData, setMyCoursesData] = useState(0);
  const [danger, setDanger] = useState("danger");
  const [status, setStatus] = useState("");
  const [refreshed, setRefreshed] = useState(false);
  useEffect(() => {
    if (!refreshed) {
      refreshCourses();
      setRefreshed(true);
    }
  });
  const createClass = () => {
    if (newClassName === "") {
      setStatus("Please enter a valid class name.");
    } else {
      props.api
        .post("/createClass", {
          id: localStorage.getItem("Tmail"),
          password: localStorage.getItem("Tpassword"),
          corskey: localStorage.getItem("corskey"),
          className: newClassName,
        })
        .then((res) => {
          setStatus(res.data);
          if (res.data === "Class created successfully.") {
            setDanger("success");
          } else {
            setDanger("danger");
          }
        })
        .catch((e) => {
          console.log(e);
        });
    }
  };
  const refreshCourses = () => {
    props.api
      .post("/refreshClasses", {
        id: localStorage.getItem("Tmail"),
        password: localStorage.getItem("Tpassword"),
        courseKey: localStorage.getItem("corskey"),
      })
      .then((res) => {
        let corslst = res.data;
        corslst.reverse();
        setCoursesList(corslst);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  return (
    <>
      <div className="container mb-3 sent-message pt-2 pb-2">
        Course Key: {localStorage.getItem("corskey")}
        <br />
        Course Name: {localStorage.getItem("corsname")}
      </div>

      <form
        onSubmit={(e) => {
          e.preventDefault();
          createClass();
        }}
      >
        <div className="text-center mb-4">
          <p className="mb-1">
            Please write below the name of the Class you want to create for the
            course {localStorage.getItem("corsname")}:
          </p>
          <div className="mb-2">
            <input
              onFocus={(e) => {
                e.target.select();
              }}
              className="form-control border-secondary text-center bg-dark container rounded"
              type="text"
              defaultValue={currdate()}
              placeholder="Class Name"
              onInput={(i) => {
                setNewClassName(i.target.value);
              }}
            />
          </div>
          <button type="submit" className="button2">
            Create class
          </button>

          {/* Status is shown here */}
          <div className={"text-center text-" + danger}>{status}</div>
        </div>
      </form>

      <div className="mb-4 container p-0">
        <div>
          <button onClick={refreshCourses} className="btn button2 mb-3">
            Refresh
          </button>
        </div>
        <button
          onClick={() => {
            myCoursesData === 0 ? setMyCoursesData(1) : setMyCoursesData(0);
          }}
          className="btn w-100 border-0"
        >
          Classes created for the course {localStorage.getItem("corsname")}{" "}
          {myCoursesData === 0 ? "" : ""}
        </button>

        <div className="border">
          <Collapse in={myCoursesData === 1}>
            <div className="text-center" style={{ backgroundColor: "#055650" }}>
              {coursesList.length > 0 ? (
                <>
                  <div className="p-2 fw-bold bg-dark">Class Name</div>
                  {coursesList.map((data, index) => (
                    <div
                      style={{ cursor: "pointer" }}
                      className="border-top p-2"
                      // This is what happens when you click on a class
                      onClick={() => {
                        localStorage.setItem("classname", data);
                        navigateToCourse();
                      }}
                      key={index}
                    >
                      {data}
                    </div>
                  ))}
                </>
              ) : (
                "There are no classes to show."
              )}
            </div>
          </Collapse>
        </div>
      </div>
    </>
  );
}
