import { useState } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

export default function TeacherReg(props) {
  const navigate = useNavigate();
  const navigateToLogin = () => {
    navigate("/Teacher_login");
  };

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [status, setStatus] = useState("");
  return (
    <>
      <div className="container mt-30vh">
        <div className="mb-3">
          <h4>Register as a teacher</h4>
          <p>
            When you register as a teacher, you can then take and track the
            attendance of students. <br /> Remember to remember the password as
            there is no option to reset it as of now...
          </p>
        </div>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            setStatus("Please wait until we process the request...");
            props.api
              .post("/registerTeacher", {
                name: name,
                email: email,
                password: password,
              })
              .then((response) => {
                const data = response.data;
                setStatus(data);

                if (
                  data === "User Created Successfully, Please proceed to login."
                ) {
                  navigateToLogin();
                } else {
                  setStatus(data);
                }
              })
              .catch((err) => {
                setStatus("Server did not respond!");
              });
          }}
        >
          <div className="mb-3">
            <input
              required
              type="name"
              placeholder="Name"
              className="form-control"
              onInput={(e) => setName(e.target.value)}
            />
          </div>
          <div className="mb-3">
            <input
              required
              type="email"
              placeholder="Email Address"
              className="form-control"
              onInput={(e) => setEmail(e.target.value)}
            />
          </div>
          <div className="mb-4">
            <input
              required
              type="password"
              placeholder="Password"
              className="form-control"
              onInput={(e) => setPassword(e.target.value)}
            />
          </div>
          <button type="submit" className="btn button2 mb-2">
            Register as a teacher
          </button>
          <div className="sent-message mb-2">{status}</div>
        </form>
        <div className="mb-1">Register as:</div>
        <div className="mb-3">
          <Link to="/Teacher_register">
            <button className="btn button2 me-3 selected">Teacher</button>
          </Link>
          <Link to="/Register">
            <button className="btn button2">Student</button>
          </Link>
        </div>
        <div className="mb-1">Login as:</div>
        <div>
          <Link to="/Teacher_login">
            <button className="btn button2 me-3">Teacher</button>
          </Link>
          <Link to="/login">
            <button className="btn button2">Student</button>
          </Link>
        </div>
      </div>
    </>
  );
}
