import "./App.css";
import Navbar from "./Componants/Navbar";
import Teacher from "./Componants/Teacher";
import Register from "./Componants/Register";
import Login from "./Componants/Login";
import LoggedIn from "./Componants/LoggedIn";
import TeacherReg from "./Componants/TeacherReg";
import TeacherLog from "./Componants/TeacherLog";
import TDashboard from "./Componants/TDashboard";
import SDashboard from "./Componants/SDashboard";
import TClasses from "./Componants/TClasses";

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import { useState } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import axios from "axios";

const api = axios.create({
  baseURL: "http://localhost:3000/",
});

const App = () => {
  const [active, setActive] = useState(0);
  const [darkMode, setDarkMode] = useState(
    true
    // localStorage.getItem("darkMode") === "true" ? true : false
  );

  const dark = darkMode ? "dark" : "light";
  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
    localStorage.setItem("darkMode", !darkMode);
  };
  return (
    <Router>
      <div className={dark + " transition main crsrdflt"}>
        {/* <ModalAlert /> */}
        <Navbar darkMode={darkMode} active={active} setActive={setActive} />
        <Routes>
          <Route
            index
            element={
              <>
                <Register api={api} />
              </>
            }
          />
          <Route
            path="Login"
            element={
              <>
                <Login api={api} />
              </>
            }
          />
          <Route
            path="Register"
            element={
              <>
                <Register api={api} />
              </>
            }
          />
          <Route
            path="contact-us"
            element={
              <>
                <div className="pt-3">{/* <ContactUs /> */}</div>
              </>
            }
          />
          <Route
            path="Course_Dashboard"
            element={
              <>
                <div className="pt-3">
                  <Teacher api={api} />
                </div>
              </>
            }
          />
          <Route
            path="Student_Course_Dashboard"
            element={
              <>
                <div className="pt-3">
                  <LoggedIn api={api} />
                </div>
              </>
            }
          />
          <Route
            path="Teacher_register"
            element={
              <>
                <div>
                  <TeacherReg api={api} />
                </div>
              </>
            }
          />
          <Route
            path="Teacher_login"
            element={
              <div>
                <TeacherLog api={api} />
              </div>
            }
          ></Route>
          <Route
            path="Teacher_dashboard"
            element={
              <div>
                <TDashboard api={api} />
              </div>
            }
          ></Route>
          <Route
            path="Student_dashboard"
            element={
              <div>
                <SDashboard api={api} />
              </div>
            }
          ></Route>
          <Route
            path="Course_classes"
            element={
              <div className="pt-3">
                <TClasses api={api} />
              </div>
            }
          ></Route>
        </Routes>

        {/* <div className="darkAosDiv text-end">
          <button onClick={toggleDarkMode} className="button3 me-2 mb-2">
            Dark Mode
          </button>
        </div> */}
        {/* <Copyright /> */}
        {/* <ProgressBar bgcolor={darkMode ? "#5f697c" : "#f75b00"} height="3px" /> */}
      </div>
    </Router>
  );
};

export default App;
