import { Link } from "react-router-dom";

export default function Navbar(props) {
  const dark = props.darkMode ? "dark" : "light";

  return (
    <>
      <nav
        id="navbar"
        className={
          "navbar navbar-expand-lg navbar-" + dark + " transition bg-" + dark
        }
      >
        <div className="container-lg">
          <a
            className="navbar-brand"
            href="/#"
            onClick={() => {
              props.setActive(0);
            }}
          >
            Geolocation Attendance App
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 0 ? " active" : "")}
                  aria-current="page"
                  to="/"
                  onClick={() => {
                    props.setActive(0);
                  }}
                >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 1 ? " active" : "")}
                  to="/Login"
                  onClick={() => {
                    props.setActive(1);
                  }}
                >
                  Login
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 2 ? " active" : "")}
                  to="/Register"
                  onClick={() => {
                    props.setActive(2);
                  }}
                >
                  Register
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 3 ? " active" : "")}
                  to="/contact-us"
                  onClick={() => {
                    props.setActive(3);
                  }}
                >
                  Contact
                </Link>
              </li>
              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle"
                  href="/#"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  More Apps
                </a>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <a href="/#" className="dropdown-item">
                      Will be added soon...
                    </a>
                  </li>
                  {/* <li>
                    <a className="dropdown-item" href="#">
                      Another action
                    </a>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Something else here
                    </a>
                  </li> */}
                </ul>
              </li>
              <li className="nav-item">
                <a
                  className="nav-link disabled"
                  href="/#"
                  tabIndex="-1"
                  aria-disabled="true"
                >
                  Developer Login
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
