import { useState } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

const Register = (props) => {
  const navigate = useNavigate();

  const navigateToLogin = () => {
    navigate("/login");
  };

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [data, setData] = useState("");
  return (
    <>
      <div className="container mt-30vh">
        <div className="row justify-content-center">
          <div>
            <form
              onSubmit={(e) => {
                e.preventDefault();
                setData("Please wait while we process your request...");
                props.api
                  .post("/register", {
                    name: name,
                    email: email,
                    password: password,
                  })
                  .then((response) => {
                    const data = response.data;
                    setData(data);

                    if (
                      data ===
                      "User Created Successfully, Please proceed to login."
                    ) {
                      navigateToLogin();
                    }
                  })
                  .catch((err) => {
                    setData("Server did not respond!");
                  });
              }}
            >
              <div className="mb-3">
                <h4>Register as a student</h4>
                <p>
                  When you register as a student, you can then mark your
                  presence for classes you attend. <br />
                  Remember to remember the password as there is no option to
                  reset it as of now...
                </p>
              </div>
              <div className="mb-3">
                <input
                  required
                  placeholder="Name"
                  type="Name"
                  className="form-control"
                  id="Name"
                  aria-describedby="NameHelp"
                  name="Name"
                  onInput={(e) => setName(e.target.value)}
                />
              </div>

              <div className="mb-3">
                <label
                  htmlFor="exampleInputEmail1"
                  className="form-label d-none"
                >
                  Email address
                </label>
                <input
                  required
                  placeholder="Email Address"
                  type="email"
                  className="form-control"
                  id="Email"
                  aria-describedby="emailHelp"
                  name="Email"
                  onInput={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className="mb-4">
                <label htmlFor="name" className="form-label d-none">
                  Password
                </label>
                <input
                  required
                  placeholder="Password"
                  type="password"
                  className="form-control txtbox"
                  id="name"
                  onInput={(e) => setPassword(e.target.value)}
                />
              </div>

              <div>
                <button type="submit" className="btn button2 mb-2">
                  Register
                </button>
                <div className="sent-message mb-2">{data}</div>
              </div>
            </form>
            <div className="mb-1">Register as:</div>
            <div className="mb-3">
              <Link to="/Teacher_register">
                <button className="btn button2 me-3">Teacher</button>
              </Link>
              <Link to="/Register">
                <button className="btn button2 selected">Student</button>
              </Link>
            </div>
            <div className="mb-1">Login as:</div>
            <div>
              <Link to="/Teacher_login">
                <button className="btn button2 me-3">Teacher</button>
              </Link>
              <Link to="/login">
                <button className="btn button2">Student</button>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Register;
