import { useState } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

function Login(props) {
  const navigate = useNavigate();
  const navigateToLoggedIn = () => {
    navigate("/Student_dashboard");
  };

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [data, setData] = useState("");
  return (
    <>
      <div className="container mt-30vh">
        <div className="mb-3">
          <h4>Login as a student</h4>
          <p>
            Use credentials which you used while registering. <br /> Register if
            you haven't already.
          </p>
        </div>
        <div className="row justify-content-center">
          <div>
            <form
              onSubmit={(e) => {
                e.preventDefault();
                setData("Please wait until we process your login request!");
                props.api
                  .post("/login", {
                    email: email,
                    password: password,
                  })
                  .then((res) => {
                    const response = res.data;
                    if (
                      response[0] === "Logged in successfully." ||
                      response[0] === "Already logged in."
                    ) {
                      setData(response);

                      localStorage.setItem("Smail", email);
                      localStorage.setItem("Spassword", password);
                      localStorage.setItem("Sname", response[1]);
                      navigateToLoggedIn();
                    } else {
                      setData(response);
                    }
                  })
                  .catch((err) => {
                    setData("Server did not respond!");
                    console.log(err);
                  });
              }}
            >
              <div className="mb-3">
                <input
                  required
                  placeholder="Email Address"
                  type="email"
                  className="form-control"
                  id="Email"
                  aria-describedby="emailHelp"
                  name="Email"
                  onInput={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className="mb-4">
                <label htmlFor="name" className="form-label d-none">
                  Password
                </label>
                <input
                  required
                  placeholder="Password"
                  type="password"
                  className="form-control txtbox"
                  id="name"
                  onInput={(e) => setPassword(e.target.value)}
                />
              </div>

              <button type="submit" className="btn button2 mb-2">
                Login
              </button>

              <div className="sent-message mb-2">{data}</div>
            </form>
            <div className="mb-1">Register as:</div>
            <div className="mb-3">
              <Link to="/Teacher_register">
                <button className="btn button2 me-3">Teacher</button>
              </Link>
              <Link to="/Register">
                <button className="btn button2">Student</button>
              </Link>
            </div>
            <div className="mb-1">Login as:</div>
            <div>
              <Link to="/Teacher_login">
                <button className="btn button2 me-3">Teacher</button>
              </Link>
              <Link to="/login">
                <button className="btn button2 selected">Student</button>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
