import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

export default function SDashboard(props) {
  const [loaded, setLoaded] = useState(false);
  useEffect(() => {
    if (!loaded) {
      refreshCourses();
      setLoaded(true);
    }
  });
  const navigate = useNavigate();
  const navigateToCourse = () => {
    navigate("/Student_Course_Dashboard");
  };
  const [coursesList, setCoursesList] = useState([]);
  const refreshCourses = () => {
    props.api
      .post("/enrolledCoursesRefresh", {
        email: localStorage.getItem("Smail"),
        password: localStorage.getItem("Spassword"),
      })
      .then((res) => {
        const corslst = res.data.reverse();
        setCoursesList(corslst);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  window.onload = () => {
    refreshCourses();
  };
  const [myCoursesData, setMyCoursesData] = useState(0);
  const [addCourse, setAddCourse] = useState(0);
  const [status, setStatus] = useState("");
  const [uniqueId, setUniqueID] = useState("");
  return (
    <>
      <div className="container mt-3">
        <div className="mb-3">
          <div className="mb-3">
            <div className="sent-message">
              {localStorage.getItem("Tname")
                ? "Welcome, " + localStorage.getItem("Sname")
                : ""}
              <br />
              {localStorage.getItem("Tmail")
                ? "Email: " + localStorage.getItem("Smail")
                : ""}
            </div>
          </div>
          <div className="mb-4">
            <button onClick={refreshCourses} className="btn button2 mb-3">
              Refresh
            </button>
            <button
              onClick={() => {
                myCoursesData === 0 ? setMyCoursesData(1) : setMyCoursesData(0);
              }}
              className="btn br-none w-100 border-0"
            >
              Courses Enrolled {myCoursesData === 0 ? "" : ""}
            </button>
            {myCoursesData === 0 ? (
              ""
            ) : (
              <div className="sent-message">
                {coursesList.length > 0 ? (
                  <table className="table text-white br-none">
                    <thead>
                      <tr>
                        <th className="bg-dark" scope="col">
                          Unique ID
                        </th>
                        <th className="bg-dark" scope="col">
                          Course Name
                        </th>
                      </tr>
                    </thead>
                    {coursesList.map((data) => (
                      <tbody
                        key={data[0]}
                        style={{ cursor: "pointer" }}
                        onClick={() => {
                          localStorage.setItem("scorsname", data[1]);
                          localStorage.setItem("scorskey", data[0]);
                          navigateToCourse();
                        }}
                      >
                        <tr>
                          <td>{data[0]}</td>
                          <td>{data[1]}</td>
                        </tr>
                      </tbody>
                    ))}
                  </table>
                ) : (
                  "Join a course to get started."
                )}
              </div>
            )}
          </div>
          <div>
            <button
              onClick={() => {
                addCourse === 0 ? setAddCourse(1) : setAddCourse(0);
              }}
              className="btn toggler w-100 border-0"
            >
              Join Courses {addCourse === 0 ? "" : ""}
            </button>
            {addCourse === 0 ? (
              ""
            ) : (
              <>
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    setStatus("Please wait while we process your request.");
                    props.api
                      .post("/enrolInCourse", {
                        email: localStorage.getItem("Smail"),
                        password: localStorage.getItem("Spassword"),
                        uniqueId: uniqueId,
                      })
                      .then((res) => {
                        setStatus(res.data);
                      })
                      .catch((e) => {
                        console.log(e);
                      });
                  }}
                >
                  <div className="bg-dark">
                    <input
                      required
                      onInput={(e) => {
                        setUniqueID(e.target.value);
                      }}
                      className="form-control border-secondary text-center bg-dark"
                      type="text"
                      placeholder="Unique key"
                    />
                  </div>
                  <button className="btn button2">Join Course</button>
                  <div className="sent-message">{status}</div>
                </form>
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
}
