import { useState } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

export default function TeacherLog(props) {
  const navigate = useNavigate();
  const navigateToDashboard = () => {
    navigate("/Teacher_dashboard");
  };
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [status, setStatus] = useState("");

  return (
    <>
      <div className="container mt-30vh background-danger">
        <div className="mb-3">
          <h4>Login as a teacher</h4>
          <p>
            Use credentials which you used while registering. <br /> Register if
            you haven't already.
          </p>
        </div>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            setStatus("Please wait while we process your request...");
            props.api
              .post("/loginTeacher", {
                email: email,
                password: password,
              })
              .then((res) => {
                const response = res.data;
                if (response[0] === "Success") {
                  localStorage.setItem("Tmail", email);
                  localStorage.setItem("Tname", response[1]);
                  localStorage.setItem("Tpassword", password);
                  navigateToDashboard();
                } else {
                  setStatus(response);
                }
              })
              .catch((e) => setStatus(e));
          }}
        >
          <div className="mb-3">
            <input
              required
              type="email"
              placeholder="Email"
              className="form-control"
              onInput={(e) => {
                setEmail(e.target.value);
              }}
            />
          </div>
          <div className="mb-4">
            <input
              required
              type="password"
              placeholder="Password"
              className="form-control"
              onInput={(e) => {
                setPassword(e.target.value);
              }}
            />
          </div>
          <div>
            <button type="submit" className="btn button2 mb-2">
              Login as a teacher
            </button>
            <div className={"sent-message mb-2"}>{status}</div>
          </div>
        </form>
        <div className="mb-1">Register as:</div>
        <div className="mb-3">
          <Link to="/Teacher_register">
            <button className="btn button2 me-3">Teacher</button>
          </Link>
          <Link to="/Register">
            <button className="btn button2">Student</button>
          </Link>
        </div>
        <div className="mb-1">Login as:</div>
        <div>
          <Link to="/Teacher_login">
            <button className="btn button2 me-3 selected">Teacher</button>
          </Link>
          <Link to="/login">
            <button className="btn button2">Student</button>
          </Link>
        </div>
      </div>
    </>
  );
}
