import { useEffect, useState } from "react";
import Collapse from "react-bootstrap/Collapse";

const Teacher = (props) => {
  const [refreshed, setRefreshed] = useState(false);
  useEffect(() => {
    if (!refreshed) {
      refresh(attendanceStarted);
      // console.log(getAttendance());
      setRefreshed(true);
    }
  });

  const getPercentage = async (smail) => {
    return await props.api
      .post("/Percentage", {
        email: localStorage.getItem("Tmail"),
        password: localStorage.getItem("Tpassword"),
        smail: smail,
        corskey: localStorage.getItem("corskey"),
      })
      .then((res) => {
        return parseInt(res.data);
      })
      .catch((e) => {
        console.log(e);
        setServerError("Server Error.");
      });
  };

  const getAttendance = async (registeredUsers) => {
    let arr = [];
    for (let i = 0; i < registeredUsers.length; i++) {
      arr.push(
        await getPercentage(
          registeredUsers[i].email,
          localStorage.getItem("corsname")
        )
      );
    }
    setPercentList(arr);
  };

  const refresh = (astarted) => {
    navigator.geolocation.getCurrentPosition(
      (position) => {
        props.api
          .post("/Teacher", {
            attendanceStarted: astarted,
            email: localStorage.getItem("Tmail"),
            password: localStorage.getItem("Tpassword"),
            name: localStorage.getItem("corsname"),
            key: localStorage.getItem("corskey"),
            distance: distance,
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
            classname: localStorage.getItem("classname"),
          })
          .then((res) => {
            if (typeof res.data === "object") {
              setNearUsers(res.data.usersInRange);
              setRegUsers(res.data.users);
              getAttendance(res.data.users);
              setServerError("");
            } else {
              setServerError(res.data);
            }
          })
          .catch((err) => {
            console.log(err);
            setServerError("Server did not respond.");
          });
      },
      (error) => {
        setServerError("Location not accessible");
      }
    );
  };

  let [serverError, setServerError] = useState("");
  const [percentList, setPercentList] = useState([]);
  const [status, setStatus] = useState("Refreshed Successfully.");
  const [refOpac, setRefOpac] = useState(false);
  const [showSetDistance, setshowSetDistance] = useState(false);
  const [regStatus, setRegStatus] = useState(-1);
  const [regUsers, setRegUsers] = useState([]);
  const [nearStatus, setNearStatus] = useState(-1);
  const [nearUsers, setNearUsers] = useState([]);
  const [distance, setDistance] = useState(25);
  const [attendanceStarted, setAttendanceStarted] = useState(false);

  return (
    <>
      <div className="container pt-5">
        <div className="mb-3 sent-message pt-2 pb-2">
          Course Key: {localStorage.getItem("corskey")}
          <br />
          Course Name: {localStorage.getItem("corsname")}
          <br />
          Class Name: {localStorage.getItem("classname")}
        </div>

        <div name="Refresh Button" className="d-flex flex-row pt-2">
          <button
            onClick={(e) => {
              setStatus("Refreshed successfully.");
              refresh(attendanceStarted);
              setRefOpac(true);
              setTimeout(() => {
                setRefOpac(false);
              }, 3000);
            }}
            className={"btn border-0 ps-1 pt-0"}
          >
            Refresh
          </button>
          {/* Status is shown here */}
          <div className="mb-2 w-100">
            <Collapse in={refOpac} dimension="height">
              <div className="sent-message refreshed">{status}</div>
            </Collapse>
          </div>
        </div>

        {/* Server error is shown here */}
        <div className="bg-danger text-center rounded w-100 mb-2">
          {serverError}
        </div>

        <div name="Set Distance" className="mb-3">
          <button
            onClick={() => setshowSetDistance(!showSetDistance)}
            className={"btn border-0 ps-1"}
          >
            Set Distance {showSetDistance ? "" : ""}
          </button>

          <Collapse in={showSetDistance}>
            <div className={"text-center bg-dark rounded"}>
              <>
                <p style={{ margin: "0" }}>
                  <br />
                  <span style={{ marginRight: "10px" }}>
                    Distance(in meters):
                  </span>
                  <input
                    style={{
                      backgroundColor: "#1d2024",
                      borderRadius: "5px",
                      border: "none",
                      width: "65px",
                      color: " white",
                      fontWeight: "bold",
                      paddingLeft: "10px",
                      marginRight: 10,
                    }}
                    defaultValue="25"
                    type="number"
                    onChange={(e) => {
                      setDistance(e.target.value);
                    }}
                  />

                  <button
                    onClick={() => {
                      refresh(attendanceStarted);
                      setStatus("Distance set successfully.");
                      setRefOpac(true);
                      setTimeout(() => {
                        setRefOpac(false);
                      }, 3000);
                    }}
                    style={{
                      backgroundColor: "#1d2024",
                      borderRadius: "5px",
                      border: "none",
                      color: " white",
                      marginRight: 20,
                    }}
                  >
                    Set
                  </button>

                  <button
                    onClick={() => {
                      if (!attendanceStarted) {
                        setStatus("Attendance has been started.");
                        setRefOpac(true);
                        setTimeout(() => {
                          setRefOpac(false);
                        }, 3000);
                        setAttendanceStarted(true);
                        refresh(true);
                      } else {
                        setStatus("Attendance has been stopped.");
                        setRefOpac(true);
                        setTimeout(() => {
                          setRefOpac(false);
                        }, 3000);
                        setAttendanceStarted(false);
                        refresh(false);
                      }
                    }}
                    style={{
                      backgroundColor: "#1d2024",
                      borderRadius: "5px",
                      border: "none",
                      color: " white",
                    }}
                  >
                    {" "}
                    {attendanceStarted ? "Stop" : "Start"} Attendance
                  </button>
                </p>
                <br />
              </>
            </div>
          </Collapse>
        </div>

        <div name="Registered Users" className="mb-3">
          <div>
            <button
              onClick={() => {
                if (regStatus !== 1) {
                  setRegStatus(1);
                } else {
                  setRegStatus(-1);
                }
              }}
              className={"btn border-0 ps-1"}
            >
              Registered Users {regStatus === 1 ? "" : ""}
            </button>
          </div>

          <Collapse in={regStatus === 1}>
            <div
              className="text-center bg-dark text-white"
              style={{ borderRadius: "10px" }}
            >
              <>
                <p
                  className="pt-2 pb-2 font-weight m-0"
                  style={{ fontSize: "16px" }}
                >
                  {regUsers.length}
                  {regUsers.length === 1
                    ? " registered user found."
                    : " registered users found."}
                </p>
                {regUsers.length === 0 ? (
                  ""
                ) : (
                  <div className="justify-content-between d-flex text-center m-0 border-top fw-bold">
                    <span
                      className="ps-2 pe-2 border-end pb-2 pt-2"
                      style={{
                        flex: 0.2,
                      }}
                    >
                      Index
                    </span>
                    <span
                      className="ps-2 pe-2 border-end pb-2 pt-2"
                      style={{ flex: 0.4 }}
                    >
                      Name
                    </span>
                    <span
                      className="ps-2 pe-2 border-end pb-2 pt-2"
                      style={{ flex: 0.5 }}
                    >
                      Email Address
                    </span>
                    <span className="ps-2 pe-2 pb-2 pt-2" style={{ flex: 0.4 }}>
                      Attendance
                    </span>
                  </div>
                )}
                <div>
                  {regUsers.map((user, index) => (
                    <div
                      className="justify-content-between d-flex text-center border-top  font-weight-normal"
                      key={index}
                    >
                      <span
                        className="ps-2 pe-2 border-end pb-2 pt-2"
                        style={{
                          flex: 0.2,
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                      </span>
                      <span
                        className="ps-2 pe-2 border-end pb-2 pt-2"
                        style={{
                          flex: 0.4,
                        }}
                      >
                        {user.name}
                      </span>
                      <span
                        className="ps-2 pe-2 border-end pb-2 pt-2"
                        style={{
                          flex: 0.5,
                        }}
                      >
                        {user.email}
                      </span>
                      <span
                        className="ps-2 pe-2 pb-2 pt-2"
                        style={{
                          flex: 0.4,
                        }}
                      >
                        {percentList[index]}%
                      </span>
                    </div>
                  ))}
                </div>
              </>
            </div>
          </Collapse>
        </div>

        <div name="Users in range" className="mb-3">
          <div>
            <button
              onClick={() => {
                if (nearStatus === -1) {
                  setNearStatus(1);
                } else {
                  setNearStatus(-1);
                }
              }}
              className={"btn border-0 ps-1"}
            >
              Users in range {nearStatus === 1 ? "" : ""}
            </button>
          </div>

          <Collapse in={nearStatus === 1}>
            <div
              className="bg-dark text-center text-white"
              style={{ borderRadius: "10px" }}
            >
              <>
                <p className="pt-2 pb-2 font-weight m-0">
                  {nearUsers.length}
                  {nearUsers.length === 1
                    ? " user found in the range."
                    : " users found in the range."}
                </p>
                {nearUsers.length === 0 ? (
                  ""
                ) : (
                  <div className="justify-content-between d-flex text-center fw-bold m-0 border-top">
                    <span
                      className="ps-2 pe-2 border-end pb-2 pt-2"
                      style={{
                        flex: 0.2,
                      }}
                    >
                      Index
                    </span>
                    <span
                      className="ps-2 pe-2 border-end pb-2 pt-2"
                      style={{
                        flex: 0.5,
                      }}
                    >
                      Name
                    </span>
                    <span
                      className="ps-2 pe-2 pb-2 pt-2"
                      style={{
                        flex: 0.5,
                      }}
                    >
                      Email Address
                    </span>
                  </div>
                )}
                <div>
                  {nearUsers.map((user, index) => (
                    <div
                      className="justify-content-between d-flex text-center border-top"
                      key={index}
                    >
                      <span
                        className="ps-2 pe-2 border-end pb-2 pt-2"
                        style={{
                          flex: 0.2,
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                      </span>
                      <span
                        className="ps-2 pe-2 border-end pb-2 pt-2"
                        style={{
                          flex: 0.5,
                        }}
                      >
                        {user.name}
                      </span>
                      <span
                        className="ps-2 pe-2 pb-2 pt-2"
                        style={{
                          flex: 0.5,
                        }}
                      >
                        {user.email}
                      </span>
                    </div>
                  ))}
                </div>
              </>
            </div>
          </Collapse>
        </div>
      </div>
    </>
  );
};

export default Teacher;
